import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

import { environment } from 'src/environments/environment';

export const API_URL = `${environment.apiUrl}/DigitalSignatureAPI/`;

@Injectable({
   providedIn: 'root',
})
export class ApiService {
   constructor(private http: HttpClient) { }


   get(path: string, options?: any): Observable<any> {
      return this.http.get(API_URL + path, options);
   }

   post(path: string, options?: any): Observable<any> {
      return this.http.post(API_URL + path, options);
   }

   put(path: string, options?: any): Observable<any> {
      return this.http.put(API_URL + path, options);
   }

   patch(path: string, options?: any): Observable<any> {
      return this.http.patch(API_URL + path, options);
   }

   delete(path: string, options?: any): Observable<any> {
      return this.http.delete(API_URL + path, options);
   }
}
