import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { User } from '../models/auth.models';
import { environment } from 'src/environments/environment';
const API_USERS_URL = `${environment.apiUrl}/DigitalSignatureAPI/api/Auth/`;

@Injectable({ providedIn: 'root' })
export class AuthService {
   private currentUserSubject: BehaviorSubject<User>;
   public currentUser: Observable<User>;

   constructor(private http: HttpClient) {
      this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
      this.currentUser = this.currentUserSubject.asObservable();
   }

   public get currentUserValue(): any {
      return this.currentUserSubject.value;
   }

   login(obj: { username: string; password: string }) {
      const credentials = {
         UserId: obj.username,
         username: obj.username,
         password: obj.password
      }
      return this.http.post<any>(`${API_USERS_URL}Login`, credentials)
         .pipe(map(user => {
            // login successful if there's a jwt token in the response
            if (user?.Data && user?.Data?.JWToken) {
               // store user details and jwt token in local storage to keep user logged in between page refreshes
               localStorage.setItem('currentUser', JSON.stringify(user.Data));
               this.currentUserSubject.next(user.Data);
            }
            return user.Data;
         }));
   }

   logout() {
      // remove user from local storage to log user out
      localStorage.removeItem('currentUser');
      localStorage.setItem('lastUser', 'true');
      this.currentUserSubject.next(null);
   }
}
