import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

// import { AuthXService } from '../services/authx.service';
import { AuthService } from '../services/auth.service';

// import { environment } from '../../../environments/environment';

@Injectable({ providedIn: 'root' })
export class AuthGuard implements CanActivate {
   constructor(
      private router: Router,
      //   private AuthService: AuthXService,
      private authFackservice: AuthService
   ) { }

   canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
      //   if (environment.defaultauth === 'firebase') {
      //       const currentUser = this.AuthXService.currentUser();
      //       if (currentUser) {
      //           // logged in so return true
      //           return true;
      //       }
      //   } else {
      const currentUser = this.authFackservice.currentUserValue;
      if (currentUser) {
         const arr = currentUser.AUA_Code?.split(',');
         if (route.data.role && !arr.includes(route.data.role)) {
            this.router.navigate(['/']);
            return false;
         }

         // logged in so return true
         return true;
      }
      //   }
      // not logged in so redirect to login page with the return url
      this.router.navigate(['/account/login'], { queryParams: { returnUrl: state.url } });
      return false;
   }
}
